@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;700&display=swap");
/* alerts  onloading animations */
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

/* Typography*/
html {
  font-size: 100%;
}

/*16px*/
body {
  display: grid;
  place-items: center;
  background: white;
  font-family: "IBM Plex Sans", sans-serif;
  line-height: 1.75;
  color: #191826;
}

p {
  font-weight: 400;
  max-width: 40em;
  margin-bottom: 1.5rem;
}
p.body1 {
  font-size: 1.25rem;
  /* (20px)*/
  line-height: 34px;
}
p.body2 {
  font-size: 1rem;
  /* (16px)*/
  line-height: 26px;
}

h1,
h2 {
  margin: 0;
  margin-bottom: 1.38rem;
  font-weight: 700;
  line-height: 1.3;
  text-transform: capitalize;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}
h1.primaryHeading {
  font-size: 88px;
  line-height: 88px;
  letter-spacing: -1px;
}

h2 {
  font-size: 2.441rem;
}
h2.secondaryHeading {
  font-size: 40px;
  line-height: 52px;
}

a {
  text-decoration: none;
}

/* buttons */
.btn {
  cursor: pointer;
  color: #fcfaf9;
  background: #191826;
  border: transparent;
  border-radius: 0.75rem;
  letter-spacing: 1px;
  padding: 0.375rem 0.75rem;
  transition: 0.6s ease-in-out all;
  text-transform: capitalize;
  display: inline-block;
  /*block class is good with forms*/
}
.btn:hover {
  background: #fcfaf9;
  color: #191826;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.btn-ios {
  color: #fcfaf9;
  background: #191826;
}
.btn-ios:hover {
  background: #66e2dc;
}
.btn-android {
  color: #191826;
  background: #fcfaf9;
}
.btn-android:hover {
  background: #ffb964;
}
.btn-block {
  width: 100%;
}

/* alerts */

/* forms */

/* titles */
.title {
  text-align: center;
}

/* images */
.imgContainer {
  max-width: 100%;
}
.imgContainer .img {
  width: 100%;
  display: block;
  object-fit: cover;
}

.hero {
  width: 100%;
  display: grid;
  place-items: center;
  justify-content: start;
  gap: 30px;
}
.hero .primaryHeading {
  font-size: 40px;
  line-height: 48px;
  letter-spacing: -0.45px;
  margin: 0;
  text-transform: initial;
}
.hero .body2 {
  margin: 0;
  justify-self: start;
}

@media (min-width: 768px) {
  .hero {
    max-width: 520px;
  }
  .hero .primaryHeading {
    font-size: 64px;
    line-height: 64px;
    letter-spacing: -0.73px;
  }
  .hero .body2 {
    font-size: 18px;
    line-height: 28px;
    letter-spacing: 0;
  }
}
@media (min-width: 1440px) {
  .hero {
    max-width: 1015px;
  }
  .hero .primaryHeading {
    font-size: 88px;
    line-height: 88px;
    letter-spacing: -1px;
  }
  .hero .body2 {
    font-size: 20px;
    line-height: 34px;
    letter-spacing: 0;
  }
}
.cards {
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  border-radius: 0.75rem;
  background-color: #191826;
  position: relative;
}
.cards::before {
  content: "";
  width: 100%;
  height: 468px;
  border-radius: 100px 100px 0 0;
  position: absolute;
  top: 0;
  background: #191826 url(../assets/bg-pattern-2.svg) no-repeat center center/contain;
}

.card {
  max-width: 100%;
  width: 100%;
  display: grid;
  place-items: center;
  border-radius: 0.75rem;
  overflow: hidden;
  z-index: 9;
  position: absolute;
}
.card__header {
  width: 208.97px;
  top: -100px;
}
.card__body {
  top: 429px;
  padding: 3rem 2.25rem;
  background-color: #fa7453;
  color: #fcfaf9;
  gap: 32px;
}
.card__body .textContainer {
  display: grid;
  place-items: center;
  gap: 22px;
}
.card__body .textContainer .secondaryHeading {
  width: 100%;
  justify-self: start;
  font-size: 32px;
  line-height: 40px;
  margin-bottom: 0;
}
.card__body .textContainer .body1 {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0;
  margin: 0;
}
.card__body .price {
  justify-self: start;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.card__body .price .number {
  font-weight: 700;
  font-size: 65px;
}
.card__body .price .body2 {
  font-size: 20px;
  letter-spacing: -0.2px;
  line-height: 32px;
}
.card__body--footer {
  width: 100%;
  display: grid;
  place-items: center;
  gap: 20px;
}
.card__body--footer .btn {
  font-size: 18px;
  font-weight: 700;
  height: 60px;
  text-transform: initial;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

@media (min-width: 768px) {
  .cards {
    width: 696px;
  }
  .cards .card {
    width: 400px;
  }
  .cards .card__header {
    width: 270px;
    left: 9%;
  }
  .cards .card__body {
    right: 9%;
    top: 30%;
  }
}
@media (min-width: 1440px) {
  .cards {
    width: 1110px;
  }
  .cards .card {
    width: 446px;
  }
  .cards .card__header {
    width: 270px;
    left: 210px;
  }
  .cards .card__body {
    right: 45px;
    top: 20%;
  }
}
.container {
  width: 100%;
  max-width: 1440px;
  display: grid;
  place-items: center;
  position: relative;
  gap: 64px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  overflow: hidden;
}
.container::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  max-width: 1440px;
  padding: 0;
  background-image: url("../assets/bg-main-mobile.png");
  background-repeat: no-repeat;
  background-position: 25% -20%;
  background-size: auto;
  background-blend-mode: normal;
  z-index: 9;
}

@media (min-width: 768px) {
  .container::before {
    background-image: url("../assets/bg-main-tablet.png");
    background-repeat: no-repeat;
    background-position: 75% -25%;
    background-size: auto;
  }
  .container::after {
    content: "";
    position: absolute;
    top: -60px;
    right: -35px;
    width: 267px;
    height: 400px;
    z-index: 9;
    background: url("../assets/bg-pattern-1.svg") no-repeat center center/cover;
  }
}
@media (min-width: 1440px) {
  .container::before {
    background-image: url("../assets/bg-main-desktop.png");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: auto;
  }
  .container::after {
    width: 312px;
    height: 468px;
    top: -35px;
  }
}
.header {
  width: 100%;
  max-width: 1110px;
  padding: 0.75rem 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
  position: relative;
  background-color: transparent;
}

.nav {
  width: 100%;
  padding: 2.5rem 0;
  display: grid;
  place-items: center;
  justify-content: start;
}
.nav .imgContainer {
  width: 150px;
}

.main {
  width: 100%;
  max-width: 1110px;
  min-height: 600px;
  display: grid;
  place-items: center;
  border-radius: 0.75rem;
  margin-top: 100px;
}

@media (min-width: 768px) {
  .main {
    margin-top: 250px;
    padding: 0 1.5rem;
    justify-content: flex-start;
  }
}
@media (min-width: 1440px) {
  .main {
    margin-top: 330px;
  }
}
.footer {
  width: 100%;
  max-width: 1110px;
  margin-top: 405px;
  padding: 0.75rem 1.5rem;
  padding-bottom: 5.025rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 64px;
}
.footer .footer--header {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 32px;
}
.footer .footer--header .logo .imgContainer {
  width: 150px;
}
.footer .footer--header .credits .body2 {
  margin: 0;
}
.footer .footer--body {
  width: 100%;
  display: grid;
  place-items: center;
}
.footer .footer--body .socialLinks {
  width: 100px;
  justify-self: start;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.footer .footer--body .socialLinks svg {
  font-size: 1.5rem;
  cursor: pointer;
  transition: 0.6s ease-in-out all;
}
.footer .footer--body .socialLinks svg:hover {
  color: #fa7453;
}

@media (min-width: 768px) {
  .footer {
    margin-top: 180px;
    flex-direction: row;
  }
  .footer .footer--body {
    justify-content: flex-end;
  }
}
@media (min-width: 1440px) {
  .footer .footer--header {
    flex-direction: row;
    gap: 100px;
  }
}
*,
::after,
::before {
  box-sizing: border-box;
}

