@use '../abstracts' as *;
@use '../components/card';

.main {
  width: 100%;
  max-width: 1110px;
  min-height: $fixedWidth * 1.5;
  @include grid(center);
  border-radius: $borderRadius;
  margin-top: 100px;
}

@media (min-width: $tablets) {
  .main {
    margin-top: 250px;
    padding: 0 $padding * 2;
    justify-content: flex-start;
  }
}
@media (min-width: $bigScreens) {
  .main {
    margin-top: 330px;
  }
}
